   /* ==========================================================================
               Buttons
               ========================================================================== */
   .btn {
       font-size: 13px;
       padding: 8px 20px;
       border-radius: 30px;
       letter-spacing: 0.5px;
       font-weight: 500;
       color: #fff;
       border: none;
       text-transform: uppercase;
       -webkit-transition: all 0.3s ease-in-out;
       -moz-transition: all 0.3s ease-in-out;
       -o-transition: all 0.3s ease-in-out;
       transition: all 0.3s ease-in-out;
       display: inline-block;
   }

   .btn:focus,
   .btn:active {
       box-shadow: none;
       outline: none;
   }


   .btn.btn-light {
       color: #212529;
       background-color: #e7e7e7;
       border-color: #f8f9fa;
   }


   .btn-primary {
       background: #26ae61;
       position: relative;
       z-index: 1;
   }

   .btn-primary:hover {
       color: #fff;
       box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.15), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
   }

   .btn-primary::before {
       content: '';
       position: absolute;
       top: 0;
       left: 0;
       width: 100%;
       height: 100%;
       z-index: 1;
       border-radius: 30px;
       opacity: 0;
       transform: scale(0, 0);
       z-index: -1;
       -webkit-transition: all 0.5s ease-out 0s;
       -moz-transition: all 0.5s ease-out 0s;
       -ms-transition: all 0.5s ease-out 0s;
       -o-transition: all 0.5s ease-out 0s;
       transition: all 0.5s ease-out 0s;
   }

   .btn-primary:hover::before {
       background-color: rgba(255, 255, 255, 0.2);
       opacity: 1;
       transform: scale(1, 1);
   }

   .btn-border {
       border-style: solid;
       border-width: 2px;
       cursor: pointer;
       background-color: transparent;
       position: relative;
       border-color: #26ae61;
       color: #059945;
   }

   .btn-border.light {
       border-color: #d5dfed;
       color: #5f707e;
   }

   .btn-border:hover {
       box-shadow: 0px 7px 9px rgba(96, 94, 94, 0.17);
   }

   .btn-border::before {
       content: '';
       position: absolute;
       top: 0;
       left: 0;
       width: 100%;
       height: 100%;
       z-index: 1;
       border-radius: 30px;
       -webkit-transition: all 0.5s ease-out 0s;
       -moz-transition: all 0.5s ease-out 0s;
       -ms-transition: all 0.5s ease-out 0s;
       -o-transition: all 0.5s ease-out 0s;
       transition: all 0.5s ease-out 0s;
   }

   .btn-border:hover::before {
       background-color: rgba(255, 255, 255, 0.2);
       transform: scale(0.5, 0.5);
   }

