   /**
             * pretty-checkbox.css
             *
             * A pure CSS library to beautify checkbox and radio buttons
             *
             * Source: https://github.com/lokesh-coder/pretty-checkbox
             * Demo: https://lokesh-coder.github.io/pretty-checkbox
             *
             * Copyright (c) 2017 Lokesh rajendran
             */

   .pretty * {
       box-sizing: border-box
   }

   .pretty input:not([type=checkbox]):not([type=radio]) {
       display: none
   }

   .pretty {
       position: relative;
       display: inline-block;
       margin-right: 1em;
       white-space: nowrap;
       line-height: 1
   }

   .pretty input {
       position: absolute;
       left: 0;
       top: 0;
       min-width: 1em;
       width: 100%;
       height: 100%;
       z-index: 2;
       opacity: 0;
       margin: 0;
       padding: 0;
       cursor: pointer
   }

   .pretty .state label {
       position: initial;
       display: inline-block;
       font-weight: 400;
       margin: 0;
       text-indent: 1.5em;
       min-width: calc(1em + 2px);
       white-space: normal;
       padding-left: 22px;
       text-indent: 0px;
   }

   .pretty .state label:after,
   .pretty .state label:before {
       content: '';
       width: calc(1em + 2px);
       height: calc(1em + 2px);
       display: block;
       box-sizing: border-box;
       border-radius: 0;
       border: 1px solid transparent;
       z-index: 0;
       position: absolute;
       left: 0px;
       top: -1px;
       background-color: transparent
   }

   .pretty.pretty-sm .state label:after,
   .pretty.pretty-sm .state label:before {
       top: 0px;
   }

   .pretty.pretty-sm {
       line-height: 13px;
   }

   .pretty.pretty-sm .state label:after,
   .pretty.pretty-sm .state label:before {
       width: calc(1em + 0px);
       height: calc(1em + 0px);
   }

   .pretty.pretty-sm .state label {
       font-size: 13px;
       padding-left: 20px;
   }

   .pretty .state label:before {
       border-color: #bdc3c7;
   }

   .pretty input:disabled~.state label:before,
   .pretty input:disabled~.state label {
    border-color: #e1e6eb;
    color:#aab2b9;
}


   .pretty .state.p-is-hover,
   .pretty .state.p-is-indeterminate {
       display: none
   }

   @-webkit-keyframes zoom {
       0% {
           opacity: 0;
           -webkit-transform: scale(0);
           transform: scale(0)
       }
   }

   @keyframes zoom {
       0% {
           opacity: 0;
           -webkit-transform: scale(0);
           transform: scale(0)
       }
   }

   @-webkit-keyframes tada {
       0% {
           -webkit-animation-timing-function: ease-in;
           animation-timing-function: ease-in;
           opacity: 0;
           -webkit-transform: scale(7);
           transform: scale(7)
       }

       38% {
           -webkit-animation-timing-function: ease-out;
           animation-timing-function: ease-out;
           opacity: 1;
           -webkit-transform: scale(1);
           transform: scale(1)
       }

       55% {
           -webkit-animation-timing-function: ease-in;
           animation-timing-function: ease-in;
           -webkit-transform: scale(1.5);
           transform: scale(1.5)
       }

       72% {
           -webkit-animation-timing-function: ease-out;
           animation-timing-function: ease-out;
           -webkit-transform: scale(1);
           transform: scale(1)
       }

       81% {
           -webkit-animation-timing-function: ease-in;
           animation-timing-function: ease-in;
           -webkit-transform: scale(1.24);
           transform: scale(1.24)
       }

       89% {
           -webkit-animation-timing-function: ease-out;
           animation-timing-function: ease-out;
           -webkit-transform: scale(1);
           transform: scale(1)
       }

       95% {
           -webkit-animation-timing-function: ease-in;
           animation-timing-function: ease-in;
           -webkit-transform: scale(1.04);
           transform: scale(1.04)
       }

       100% {
           -webkit-animation-timing-function: ease-out;
           animation-timing-function: ease-out;
           -webkit-transform: scale(1);
           transform: scale(1)
       }
   }

   @keyframes tada {
       0% {
