   }

   .list-feed>li {
       position: relative;
       margin: 0;
       padding-bottom: 10px;
       padding-left: 20px;
       line-height: 18px;
   }

   .list-feed>li .text-muted {
       font-size: 11px;
   }

   .list-feed>li:before {
       content: '';
       position: absolute;
       left: 0;
       top: 5px;
       width: 8px;
       height: 8px;
       background-color: #fff;
       display: inline-block;
       border-radius: 50%;
       border: 2px solid #b6c3c9;
       z-index: 3
   }

   .list-feed>li:after {
       content: '';
       position: absolute;
       top: 5px;
       left: 3px;
       bottom: -7px;
       width: 0;
       border-left: 1px solid #b6c3c9;
       border-right: 1px solid #b6c3c9;
       z-index: 2
   }

   .list-feed>li:first-child:after {
       top: 8px
   }

   .list-feed>li:last-child {
       padding-bottom: 0
   }

   .list-feed>li:last-child:after {
       content: none
   }

   .list-feed>li[class*=border-]:after,
   .list-feed>li[class*=border-]:before {
       border-color: inherit
   }

   .list-feed-solid>li:before {
       content: '';
       width: 0;
       height: 0;
       border-width: 4px
   }

   .list-feed-square>li:before {
       content: '';
       border-radius: 0
   }

   .list-feed-rhombus>li:before {
       content: '';
       border-radius: 0;
       -webkit-transform: rotate(45deg);
       -ms-transform: rotate(45deg);
       -o-transform: rotate(45deg);
       transform: rotate(45deg)
   }

