@import '~@angular/material/prebuilt-themes/deeppurple-amber.css';

body {
  
}

.paginate_button{
    background: #fff;
   cursor: pointer;
   }
   .paginate_button:hover{
     background: #b20000;
   }
   .paginate_button.current{
     background: #b20000;
   }

/*
   server-side-angular-way.component.css
*/
.no-data-available {
  text-align: center;
}

/*
   src/styles.css (i.e. your global style)
*/
.dataTables_empty {
  display: none;
}

::ng-deep table.dataTable td.dataTables_empty {
  display: none;
}

.dataTables_wrapper .dataTables_paginate .paginate_button {
  padding:0;
}
.modal-open {
  overflow-y: hidden;
}

/* Success styling */

.mat-button.mat-success,
.mat-stroked-button.mat-success {
  color: #155724;
}
.mat-button.mat-success:hover,
.mat-stroked-button.mat-success:hover {
  background-color: #f0fff3;
}

.mat-raised-button.mat-success,
.mat-flat-button.mat-success,
.mat-fab.mat-success,
.mat-mini-fab.mat-success {
  color: #f0fff3;
  background-color: #155724;
}

.mat-icon-button.mat-success {
  color:#155724;
}

.iti {
  display: block;
}

.iti .form-control {
  padding-left: 60px!important;
}

.mat-form-field-appearance-legacy .mat-form-field-infix {
  width: 100%;
  height: 36px;
  padding: 10px 12px;
  font-size: 14px;
  border-radius: 6px;
  border: 1px solid #dae1e9;
}

.mat-form-field-appearance-legacy.mat-form-field-disabled .mat-form-field-infix {
  background: #f5f9ff;
}

.mat-form-field-appearance-legacy.mat-form-field-disabled .mat-chip.mat-standard-chip {
  background-color: #dce4ef;
  min-height: 29px;
  padding: 5px 8px;
}

.mat-form-field-appearance-legacy .mat-form-field-label {
  color: #40474e;
}
.mat-form-field-appearance-legacy.multi-select .mat-form-field-infix {
  padding: 5px 12px;
  height: 42px;
}

.mat-form-field-can-float.mat-form-field-should-float .mat-form-field-label {
  transform: translateY(-24px) translateX(-10px) scale(0.85)!important;
      color: #4e535a;
}

.mat-form-field-can-float.mat-form-field-should-float.multi-select .mat-form-field-label {
  transform: translateY(-28px) translateX(-10px) scale(0.85)!important;
}

.mat-form-field-label-wrapper {
  top: -1em;
  padding-top: 0.94375em;
}

.mat-chip-list-wrapper input.mat-input-element, .mat-chip-list-wrapper .mat-standard-chip {
  padding: 7px;
}


.filter-card-body .mat-form-field {
  display: block;
  width:100%;
}


.filter-card-body .mat-elevation-z8 {
  box-shadow: none;
  border: 1px solid #d8e3ed;
}

.filter-card-body th.mat-header-cell:first-of-type, 
.filter-card-body td.mat-cell:first-of-type, 
.filter-card-body td.mat-footer-cell:first-of-type {
  padding-left: 6px;
}

.filter-card-body tr.mat-row, .filter-card-body tr.mat-footer-row {
  height: 28px;
}

.filter-card-body .mat-form-field-appearance-legacy .mat-form-field-wrapper {
  padding-bottom:10px;
}

.ngx-slider {
  margin: 34px 0 6px!important;
}

.ngx-slider .ngx-slider-pointer {
    width: 19px!important;
    height: 19px!important;
    top: -10px!important;
    border-radius: 26px!important;
}

.ngx-slider .ngx-slider-pointer:after {
  width: 5px!important;
  height: 5px!important;
  top: 7px!important;
  left: 7px!important;
  border-radius: 10px!important;
}

.ngx-slider .ngx-slider-bubble {
  font-size: 13px!important;
  bottom: 15px!important;
}

.ngx-slider .ngx-slider-bar-wrapper {
  margin-top: -17px!important;
 }

.mat-table {
  font-family: inherit!important;
  width:100%;
} 

.mat-form-field {
  font-family: inherit!important;
}

.mat-cell, .mat-footer-cell {
  font-size: 12px;
  line-height: 15px;
}

tr.mat-header-row {
  height:30px!important;
}

  
.filter-card-body .mat-form-field-appearance-legacy .mat-form-field-underline {
  bottom:12px;
}

.filter-card-body  .example-container {
  height: auto !important;
  max-height:180px!important;
}

.filter-card-body.create-test-skills-table.card-body {
  padding:15px;
}

.filter-card-body.create-test-skills-table .example-container {
  height: 340px!important;
  max-height: 340px!important;
  overflow-y: auto;
}

.filter-card-body.view-questions-skills-table .example-container {
  height: 200px!important;
  max-height: 200px!important;
  overflow-y: auto;
}

.mat-icon-button.button-close-file {
  width: 25px;
    height: 25px;
    line-height: 22px;
}

.filter-card-body th.mat-header-cell:last-of-type,
.filter-card-body td.mat-cell:last-of-type,
.filter-card-body td.mat-footer-cell:last-of-type {
  padding-right: 6px;
}

.ngx-slider .ngx-slider-bar {
  height: 2px!important;
}

.mat-chip.mat-standard-chip {
  background-color: #eff4fd;
  font-size: 13px;
}

.mat-standard-chip {
  min-height: 30px;
}

.mat-autocomplete-panel, .mat-select-panel {
  box-shadow: 0px 3px 6px 1px #2f3a4430 !important;
    border-radius: 6px!important;
    border: 1px solid #d1dced !important;
}

.mat-standard-chip.mat-chip-with-trailing-icon {
  padding: 8px!important;
  min-height: 28px;
}

.mat-autocomplete-panel .mat-option {
  line-height: 30px;
  height: 34px;
  padding: 0 15px;
  text-align: left;
  font-size: 14px;
  text-decoration: none;
  font-family: inherit;
}

.mat-form-field-appearance-legacy .mat-form-field-underline {
  display: none;;
}

.mat-form-field-appearance-legacy .mat-form-field-label {
  top:24px;
  left: 12px;
}

.mat-form-field-appearance-legacy.multi-select  .mat-form-field-label {
  top:27px;
}

.mat-select-panel .mat-optgroup-label, .mat-select-panel .mat-option {
  line-height: 20px!important;
    height: 30px!important;
    font-family: inherit;
    font-size:13px;
}

.mat-select {
  font-family: inherit!important;
}

.mat-standard-chip .mat-chip-remove.mat-icon, .mat-standard-chip .mat-chip-remove .mat-icon {
  color: #21365a;
}

.mat-form-field-can-float.mat-form-field-should-float.resume-input .mat-form-field-label {
  display: none;
}

.mat-chip-list-wrapper input.mat-input-element, .mat-chip-list-wrapper .mat-standard-chip {
  margin: 5px;
}

.cdk-overlay-pane {
  width: auto !important;
  height: auto!important;
  max-width: 380px;
    text-align: center;
}

.cdk-overlay-container {
  z-index: 9999 !important;
}

.mat-dialog-actions {
  display: block!important;
    text-align: center;
    margin:8px 0px 0px 0px!important;
    padding: 8px 0px 0px 0px!important;
    min-height: auto!important;
}

.mat-raised-button {
    font-size: 12px;
    letter-spacing: 0;
    background-color: #26ae61!important;
    position: relative;
    z-index: 1;
    font-size: 13px;
    padding: 6px 15px;
    border-radius: 30px;
    letter-spacing: 0.5px;
    margin:0 5px!important;
    font-weight: 500;
    color: #fff;
    border: none;
    text-transform: uppercase;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    display: inline-block;
}

.iti__selected-flag.dropdown-toggle {
  width: auto!important;
}

.country-dropdown {
  max-width: 100%;
  min-width: 440px!important;
}

.country-dropdown .iti__country {
  font-size: 13px;;
}
#country-search-box:focus {
  border: 1px solid #ccc;
}

.fc .fc-toolbar.fc-header-toolbar {
  margin-bottom: 8px!important;
}
.fc .fc-button {
  padding: 3px 8px!important;
  font-size: 12px!important;
}

.fc .fc-toolbar-title {
  font-size: 18px!important;
}